
.ion-modal{position:absolute;top:0;left:0;display:block;width:100%;height:100%;contain:strict;z-index:12
}
@media not all and (min-width: 768px) and (min-height: 600px){
.ion-modal .ion-backdrop{visibility:hidden
}
}
.modal-wrapper{z-index:10;height:100%;contain:strict
}
@media only screen and (min-width: 768px) and (min-height: 600px){
.modal-wrapper{position:absolute;top:calc(50% - (500px/2));left:calc(50% - (600px/2));width:600px;height:500px
}
}
@media only screen and (min-width: 768px) and (min-height: 768px){
.modal-wrapper{position:absolute;top:calc(50% - (600px/2));left:calc(50% - (600px/2));width:600px;height:600px
}
}
.ios .modal-wrapper{transition:all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms
}
@media only screen and (min-width: 768px) and (min-height: 600px){
.ios .modal-wrapper{overflow:hidden;border-radius:10px
}
}
.md .modal-wrapper{opacity:.01;transform:translate3d(0, 40px, 0)
}
@media only screen and (min-width: 768px) and (min-height: 600px){
.md .modal-wrapper{overflow:hidden;border-radius:2px;box-shadow:0 28px 48px rgba(0,0,0,0.4)
}
}
.wp .modal-wrapper{opacity:.01;transform:translate3d(0, 40px, 0)
}
.modal-enter-active,.modal-leave-active{transform:translateY(0%);opacity:1
}
.modal-enter,.modal-leave-active{transform:translateY(100%);opacity:0
}



