
.ion-badge{display:inline-block;padding:3px 8px;min-width:10px;font-size:1.3rem;font-weight:bold;line-height:1;text-align:center;white-space:nowrap;vertical-align:baseline
}
.ion-badge:empty{display:none
}
.badge-ios{border-radius:10px;color:#fff;background-color:#327eff
}
.badge-ios-primary{color:#fff;background-color:#327eff
}
.badge-ios-secondary{color:#fff;background-color:#32db64
}
.badge-ios-danger{color:#fff;background-color:#f53d3d
}
.badge-ios-light{color:#000;background-color:#f4f4f4
}
.badge-ios-dark{color:#fff;background-color:#222
}



