
.ion-label{display:block;overflow:hidden;-ms-flex:1;flex:1;margin:0;font-size:inherit;text-overflow:ellipsis;white-space:nowrap
}
.item-input .ion-label{-ms-flex:initial;flex:initial;max-width:200px;pointer-events:none
}
