}
.ion-label[fixed]{-ms-flex:0 0 100px;flex:0 0 100px;width:100px;min-width:100px;max-width:200px
}
.item-label-stacked .ion-label,.item-label-floating .ion-label{-ms-flex-item-align:stretch;-ms-grid-row-align:stretch;align-self:stretch;width:auto;max-width:100%
}
.ion-label[stacked],.ion-label[floating]{margin-bottom:0
}
