}
.item-label-stacked .ion-select,.item-label-floating .ion-select{-ms-flex-item-align:stretch;-ms-grid-row-align:stretch;align-self:stretch;max-width:100%
}
.item-select .ion-label[floating]{transform:translate3d(0, 0, 0) scale(0.8)
