.label-ios{margin:11px 8px 11px 0
}
.label-ios+.ion-input .text-input,.label-ios+.ion-textarea .text-input,.label-ios+.input+.cloned-input{margin-left:16px;width:calc(100% - (16px / 2) - 16px)
