
.ion-action-sheet{position:absolute;top:0;left:0;z-index:1000;display:block;width:100%;height:100%
}
.action-sheet-wrapper{position:absolute;right:0;bottom:0;left:0;z-index:10;display:block;margin:auto;width:100%;max-width:500px;transition:all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms
}
.action-sheet-button{width:100%
}
.action-sheet-ios{text-align:center
}
.action-sheet-ios .action-sheet-container{padding:0 10px
}
.action-sheet-ios .action-sheet-group{overflow:hidden;margin-bottom:8px;border-radius:13px;background:#f9f9f9
}
.action-sheet-ios .action-sheet-group:last-child{margin-bottom:10px
}
.action-sheet-ios .action-sheet-title{padding:1.5rem;border-bottom:.55px solid #d6d6da;border-radius:0;font-size:1.3rem;font-weight:400;text-align:center;color:#8f8f8f
}
.action-sheet-ios .action-sheet-title .action-sheet-sub-title{margin-top:3%;font-size:77%;line-height:130%;max-width:90%;margin-left:auto;margin-right:auto
}
.action-sheet-ios .action-sheet-button{margin:0;padding:18px;min-height:5.6rem;border-bottom:.55px solid #d6d6da;font-size:2rem;color:#007aff;background:transparent
}
.action-sheet-ios .action-sheet-button:last-child{border-bottom-color:transparent
}
.action-sheet-ios .action-sheet-button:active{border-bottom-color:#ebebeb;background:#ebebeb
}
.action-sheet-ios .action-sheet-selected{font-weight:bold;background:#fff
}
.action-sheet-ios .action-sheet-destructive{color:#f53d3d
}
.action-sheet-ios .action-sheet-cancel{font-weight:600;background:#fff
}
.action-sheet-wp .action-sheet-wrapper{box-shadow:0 -1px 0 rgba(0,0,0,0.2)
}
.action-sheet-wp .action-sheet-title{padding:11px 16px 17px;font-size:2rem;text-align:left;color:#4d4d4d
}
.action-sheet-wp .action-sheet-button{padding:0 16px;min-height:4.8rem;font-size:1.5rem;text-align:left;color:#4d4d4d;background:transparent
}
.action-sheet-wp .action-sheet-button.activated{background:#aaa
}
.action-sheet-wp .action-sheet-icon{margin:0 20px 0 0;padding:0;width:2.3rem;font-size:2.4rem;text-align:center;vertical-align:middle
}
.action-sheet-wp .action-sheet-container{padding:.8rem 0;background:#fff
}
.action-sheet-wp .action-sheet-group .button-inner{-ms-flex-pack:start;justify-content:flex-start
}
.action-sheet-wp .action-sheet-selected{font-weight:bold
}
.action-sheet-wp .action-sheet-cancel{background:transparent
}
.action-sheet-md .action-sheet-container{padding:.8rem 0;background:#fafafa
}
.action-sheet-md .action-sheet-title{padding:11px 16px 17px;font-size:1.6rem;text-align:left;color:#757575
}
.action-sheet-md .action-sheet-button{position:relative;overflow:hidden;padding:0 16px;min-height:4.8rem;font-size:1.6rem;text-align:left;color:#222;background:transparent
}
.action-sheet-md .action-sheet-button.activated{background:#f1f1f1
}
.action-sheet-md .action-sheet-icon{margin:0 32px 0 0;padding:0;width:2.3rem;font-size:2.4rem;text-align:center;vertical-align:middle
}
.action-sheet-md .action-sheet-group{overflow:hidden
}
.action-sheet-md .action-sheet-group .button-inner{-ms-flex-pack:start;justify-content:flex-start
}
.action-sheet-md .action-sheet-selected{font-weight:bold
}
.action-sheet-enter-active,.action-sheet-leave-active{transform:translateY(0%)
}
.action-sheet-enter,.action-sheet-leave-active{transform:translateY(100%)
}



