
.ion-loading{position:absolute;top:0;right:0;bottom:0;left:0;z-index:1000;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;contain:strict
}
.loading-wrapper{z-index:10;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;opacity:1;transition:all ease-in-out 200ms;font-size:14px
}
.loading-ios .loading-wrapper{padding:24px 34px;max-width:270px;max-height:90%;border-radius:8px;color:#000;background:#f8f8f8;margin-bottom:44px
}
.loading-ios .loading-content{font-weight:bold
}
.loading-ios .loading-spinner+.loading-content{margin-left:16px
}
.loading-ios .spinner-ios line,.loading-ios .spinner-ios-small line{stroke:#69717d
}
.loading-ios .spinner-bubbles circle{fill:#69717d
}
.loading-ios .spinner-circles circle{fill:#69717d
}
.loading-ios .spinner-crescent circle{stroke:#69717d
}
.loading-ios .spinner-dots circle{fill:#69717d
}
.loading-md .loading-wrapper{padding:24px;max-width:280px;max-height:90%;border-radius:2px;color:rgba(0,0,0,0.5);background:#fafafa;box-shadow:0 16px 20px rgba(0,0,0,0.4)
}
.loading-md .loading-spinner+.loading-content{margin-left:16px
}
.loading-md .spinner-ios line,.loading-md .spinner-ios-small line{stroke:#327eff
}
.loading-md .spinner-bubbles circle{fill:#327eff
}
.loading-md .spinner-circles circle{fill:#327eff
}
.loading-md .spinner-crescent circle{stroke:#327eff
}
.loading-md .spinner-dots circle{fill:#327eff
}
.loading-wp .loading-wrapper{padding:20px;max-width:280px;max-height:90%;border-radius:2px;color:#fff;background:#000
}
.loading-wp .loading-spinner+.loading-content{margin-left:16px
}
.loading-wp .spinner-ios line,.loading-wp .spinner-ios-small line{stroke:#fff
}
.loading-wp .spinner-bubbles circle{fill:#fff
}
.loading-wp .spinner-circles circle{fill:#fff
}
.loading-wp .spinner-crescent circle{stroke:#fff
}
.loading-wp .spinner-dots circle{fill:#fff
}
.loading-enter-active,.loading-leave-active{transform:scale(1);opacity:1
}
.loading-enter{transform:scale(1.1);opacity:0
}
.loading-leave-active{transform:scale(0.9);opacity:0
}



