
.ion-backdrop{position:absolute;top:0;left:0;z-index:10;display:block;width:100%;height:100%;background-color:#000;opacity:.4;transform:translateZ(0)
}
.ion-backdrop.backdrop-no-tappable{cursor:auto
}
.backdrop-enter-active,.backdrop-leave-active{transition:opacity 200ms
}
.backdrop-enter,.backdrop-leave-active{opacity:0
}



