
.ion-toast{position:absolute;top:0;left:0;z-index:1000;display:block;width:100%;height:100%;pointer-events:none;contain:strict
}
.toast-container{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;pointer-events:auto;contain:content
}
.toast-container .toast-message{-ms-flex:1;flex:1
}
.toast-container .toast-button{padding:19px 16px 17px;font-size:1.5rem;color:#fff
}
.toast-ios .toast-wrapper{position:absolute;right:10px;left:10px;z-index:10;display:block;margin:auto;max-width:700px;border-radius:.65rem;background:rgba(0,0,0,0.9)
}
.toast-ios .toast-wrapper.toast-top{top:10px;transition:all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms
}
.toast-ios .toast-wrapper.toast-bottom{bottom:10px;transition:all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms
}
.toast-ios .toast-wrapper.toast-middle{top:50%;transform:translateY(-50%);transition:opacity cubic-bezier(0.36, 0.66, 0.04, 1) 400ms
}
.toast-ios .toast-message{padding:1.5rem;font-size:1.4rem;color:#fff
}
.toast-wp .toast-wrapper{position:absolute;right:0;left:0;z-index:10;display:block;margin:auto;max-width:700px;border-radius:0;background:#000
}
.toast-wp .toast-wrapper.toast-top{top:0;opacity:.01
}
.toast-wp .toast-wrapper.toast-bottom{bottom:0;opacity:.01
}
.toast-wp .toast-wrapper.toast-middle{opacity:.01
}
.toast-message{padding:1.5rem;font-size:1.4rem;color:#fff
}
.toast-button{color:#fff
}
.toast-md .toast-wrapper{position:absolute;right:0;left:0;z-index:10;display:block;margin:auto;width:100%;max-width:700px;background:#333
}
.toast-md .toast-wrapper.toast-top{top:0;transform:translate3d(0, -100%, 0)
}
.toast-md .toast-wrapper.toast-bottom{bottom:0;transform:translate3d(0, 100%, 0)
}
.toast-md .toast-wrapper.toast-middle{opacity:.01
}
.toast-md .toast-message{padding:19px 16px 17px;font-size:1.5rem;color:#fff
}
.toast-top-enter-active,.toast-top-leave-active{transform:translateY(0)
}
.toast-top-enter,.toast-top-leave-active{transform:translateY(-130%)
}
.toast-bottom-enter-active,.toast-bottom-leave-active{transform:translateY(0)
}
.toast-bottom-enter,.toast-bottom-leave-active{transform:translateY(130%)
}
.toast-middle-enter-active,.toast-middle-leave-active{opacity:1
}
.toast-middle-enter,.toast-middle-leave-active{opacity:0.01
}



