
.toggle-ios{position:relative
}
.toggle-ios .toggle-icon{position:relative;display:block;width:51px;height:32px;border-radius:16px;background-color:#e6e6e6;transition:background-color 300ms;pointer-events:none
}
.toggle-ios .toggle-icon::before{position:absolute;top:2px;right:2px;bottom:2px;left:2px;border-radius:16px;background-color:#fff;content:"";transform:scale3d(1, 1, 1);transition:transform 300ms
}
.toggle-ios .toggle-inner{position:absolute;top:2px;left:2px;width:28px;height:28px;border-radius:14px;background-color:#fff;box-shadow:0 3px 12px rgba(0,0,0,0.16),0 3px 1px rgba(0,0,0,0.1);transition:transform 300ms,width 120ms ease-in-out 80ms,left 110ms ease-in-out 80ms
}
.toggle-ios .toggle-checked{background-color:#327eff
}
.toggle-ios .toggle-activated::before,.toggle-ios .toggle-checked::before{transform:scale3d(0, 0, 0)
}
.toggle-ios .toggle-checked .toggle-inner{transform:translate3d(19px, 0, 0)
}
.toggle-ios .toggle-activated.toggle-checked::before{transform:scale3d(0, 0, 0)
}
.toggle-ios .toggle-activated .toggle-inner{width:34px
}
.toggle-ios .toggle-activated.toggle-checked .toggle-inner{left:-4px
}
.toggle-ios.toggle-disabled,.item-ios.item-toggle-disabled ion-label{opacity:.3;pointer-events:none
}
.item-ios .toggle-ios{margin:0;padding:6px 8px 5px 16px
}
.item-ios .toggle-ios[item-left]{padding:6px 16px 5px 0
}
.toggle-ios-primary .toggle-checked{background-color:#327eff
}
.toggle-ios-secondary .toggle-checked{background-color:#32db64
}
.toggle-ios-danger .toggle-checked{background-color:#f53d3d
}
.toggle-ios-light .toggle-checked{background-color:#f4f4f4
}
.toggle-ios-dark .toggle-checked{background-color:#222
}



